<template>
<div class="center" style="margin-bottom:0px;">
    <div class="center_content1">
        <div class="ke">课程体系</div>
        <div class="table-container">
            <!-- 学生端 -->
            <ul class="table-ul" :style="{height:tableHeights}">
                <li class="table-ul_li1" v-for="(vo,index) in tableData" :key="index" :style="{width:oftenWidth}">
                    <p class="table-ul-p">{{vo.year}}</p>
                    <ul>
                        <li v-for="(item,num) in vo.list" :key="num" @click="courseInfos(item)">
                            <p style="overflow: hidden; text-overflow:ellipsis; white-space: nowrap;">{{item.name}}</p>
                        </li>
                    </ul>
                </li>
            </ul>
        </div>
    </div>
</div>
</template>

<script>
import studentApi from "@/api/studentApi";
export default {
    name: "ResultQuery",
    components: {},
    props: ['pointListske'],
    data() {
        return {
            pageTitle: "排课信息",
            tableData: [],
            tableHeight: "47px",
            tableHeights: "47px",
            versionId: "",
            userIdentity: '',
            teacherData: [],
            roleName: '',
            coursesifon:[],
            yearTermsifon:[]
        };
    },
    created() {
        this.roleName = localStorage.getItem('roleName')
    },
    mounted() {
        this.tearcherList()
    },
    computed: {
        oftenWidth() {
            return `${(1 / this.tableData.length) * 100}%`;
        },
    },
    methods: {
        dataProcessing() {
            const courses = this.coursesifon;
            const yearTerms = this.yearTermsifon;
            const lengthArr = [];
            yearTerms.forEach(item => {
                item.list = []
                courses.forEach(vo => {
                    if (vo.term === item.order) {
                        item.list.push(vo)
                    }
                })
                lengthArr.push(item.list.length)

            })
            const maxLength = Math.max.apply(null, lengthArr);
            yearTerms.forEach(item => {
                const lengths = item.list.length
                if (lengths < maxLength) {
                    const leng = maxLength - lengths
                    for (let i = 0; i < leng; i++) {
                        item.list.push({
                            courseName: '',
                            courseId: ''
                        })
                    }
                }

            })

            this.tableData = yearTerms;
            let tmpArr = []
            this.tableData.forEach(item => {
                tmpArr.push(item.list.length)
            })
            this.tableHeight = `${Math.max(...tmpArr) * 47 + 100}px`
        },
        courseInfos(item) {
            if (item.id) {
                this.$router.push({
                    path: '/studentside/graduation',
                    query: {
                        id: item.id,
                        title: '课程体系'
                    }
                })
            }

        },
        tearcherList() {
            this.dataProcessing()
            this.$nextTick(() => {
                let tmp = []
                document.querySelectorAll('.table-ul_li1>ul').forEach(item => {
                    tmp.push(item.children.length)
                })
                this.tableHeights = `${(Math.max(...tmp) + 1) * 40 + 100}px`;
            })
        }

    },
    watch: {
        pointListske(val) {
            val.yearTerms = [{
                        year: "第一学期",
                        order: 1
                    },
                    {
                        year: "第二学期",
                        order: 2
                    },
                    {
                        year: "第三学期",
                        order: 3
                    },
                    {
                        year: "第四学期",
                        order: 4
                    },
                    {
                        year: "第五学期",
                        order: 5
                    },
                    {
                        year: "第六学期",
                        order: 6
                    },
                    {
                        year: "第七学期",
                        order: 7
                    },
                    {
                        year: "第八学期",
                        order: 8
                    }
                ]
            this.coursesifon = val.courses
            this.yearTermsifon = val.yearTerms
            this.versionId = val.versionId
            this.tearcherList();
        },

    }
};
</script>

<style lang="scss">
@import "../../styles/center.scss";

.center {
    .center_content1 {
        background-color: #fff;
        padding: 0;
        padding-top: 15px;
        .ke{
            margin-left: 80px;
            margin-bottom: 15px;
            font-size:20px;
            font-family:PingFang SC;
            font-weight:bold;
            color:rgba(3,35,34,1);
        }
    }
}


.table-ul {
    width: 100%;
    clear: both;
    background: #fff;
    border-radius: 10px;
    overflow: hidden;

    li {
        list-style-type: none;
        height: 46px !important;
        text-align: center;
        line-height: 46px;
        border-right: 0 !important;
        border-bottom: 0 !important;
        box-sizing: border-box;
    }

    .table-ul_li1 {
        float: left;
        position: relative;
        border: 0 !important;

        >p {
            width: 100%;
            height: 100%;
            text-align: center;
            vertical-align: center;
            background: #e5f7f3 !important;
            font-size: 12px;
            color: #323232;
            font-weight: 700;
        }

        ul {
            width: 100%;
            position: absolute;
            left: 0;
            top: 46px;

            li {
                width: 100%;
                height: 46px;
                font-size: 14px;
                color: #606266;
                cursor: pointer;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                border: 1px solid transparent !important;
                padding: 0 10px;

                &:hover {
                    border-color: #03B187 !important;
                    color: #03B187 !important;
                    box-shadow: 0px 2px 10px 0px rgba(3, 177, 135, 0.2);
                    border-radius: 5px;
                }
            }

            li:nth-of-type(even) {
                background: #F8F8F8 !important;
            }
        }
    }

    .table-ul_li {

        // border-left: 1px solid #ebeef5;
        ul {
            li {
                // border-left: 1px solid #ebeef5;
            }
        }
    }
}
</style>



