<template>
     <div :id="id" :style="{height:chartHeight,width:chartWidth}" class="scatter"></div>
</template>

<script>
import echarts from 'echarts'
export default {
 name: 'PieCircle',
 props:{
     id:{
         type:String,
         default:'pieCircle'
     },
     chartHeight:{
         type:String,
         default:'210px'
     },
     chartWidth:{
         type:String,
         default:'100%'
     },
     pieCircleData:{
         type:Array,
         default(){
             return []
         }
     },
      color:{
         type:Array,
         default(){
             return ['#00CB9A', '#3D8FFF', '#C88CFF', '#FFE098','#5FD4FF','#784EFF','#FFA351','#43DABC']
         }
     },
 },
 data() {
 return {
   chart:null,
   pointcode:''
 }
 },
 created(){},
 mounted(){
    //  setTimeout(()=>{
    //      this.pointcode = this.pieCircleData.curVal[0].pointcode
    //      console.log(this.pieCircleData.curVal[0].pointcode,'this.pieCircleData.curVal[0].pointcode')
    //      this.initChart(); 
    //  },500)
    
 },
  watch:{
     pieCircleData(curVal,oldVal){
         this.pointcode = curVal[0].pointcode
         this.initChart(); 
     },
 },
 methods:{
      initChart(){
        const self=this;
        self.chart = echarts.init(document.getElementById(self.id));
        self.chart.setOption(self.getSeriesBarOption());
        window.addEventListener('resize', function(){
            if(self.chart){
                self.chart.resize()
            }
            
        })
     },
     getSeriesBarOption(){
          const self=this
          return {
            tooltip: {
                trigger: 'item',
                formatter: "{a} {b} : {c} ({d}%)"
            },
            color:self.color,
            title: {
                text: `毕业要求指标点${this.pointcode}支撑课程权重比`,
                textStyle: {
                  color:'#aaa'
                }
                // text: `毕业要求指标点1.1支撑课程权重比`
            },
            toolbox: {
                show: true,
                top: -5,
                feature: {
                    saveAsImage: {
                        show: true,
                    },
                }
            },
            legend: {  // 图例的配置属性
                orient: 'vertical',
                left:'42%',
                align:'left',
                top:'40%',
                textStyle: {
                    color:'#8C8C8C'
                },
                height:100, //图例的高度
                itemGap:10 //图例之间的距离
            },
            series: [
                {
                    name:'',
                    radius: ['50%', '70%'],
                    hoverAnimation: false,
                    avoidLabelOverlap: false,
                    type: "pie",
                    center: ["23%", "62%"],
                    itemStyle: {
                        normal: {
                            label: {
                            position: 'inner',
                            show: false,
                            textStyle: {
                                fontSize: 12
                            },
                            
                            formatter: function(params) {
                                var name = params.name; //名字
                                var percent = params.percent; //占比
                                var value = params.value; //数量
                                if (name.length > 8) {
                                return (
                                    name.slice(0, 7) +
                                    "..." +
                                    "\n" +
                                    value +
                                    "\n" +
                                    "(" +
                                    percent +
                                    "%"    +
                                    ")" 
                                );
                                } else {
                                return name + "\n"  + value  + "\n"  + "(" + percent + "%" + ")";
                                }
                                // return name + value + percent + "%";
                              }
                            },
                            labelLine: {
                            show: true
                            }
                        },
                        emphasis: {
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowColor: "rgba(0, 0, 0, 0.5)"
                        }
                        },
                    data:self.pieCircleData 
                }
            ]
          }
     }
  },
  beforeDestroy() {
      if (!this.chart) {
        return
      }
      this.chart.dispose();
      this.chart = null;
    }
}
</script>

<style lang='scss' scoped>
</style>



