<template>
    <div style="border-radius:10px;width:31vw;margin: 30px 0px 30px 0px;float: left;position: relative;">
        <div class="className" id="PieCircle3" style="height:580px;"></div>
        <p class="echarts_p">学时分布</p>
    </div>
     
</template>
<script>
import echarts from "echarts";
export default {
    props:['chartsDatatree'],
    watch: {
        chartsDatatree(newVal,oldVal){
            let sum = 0
            let arr = []
            newVal.forEach(element => {
                let obj = {
                    name:'',
                    value:0
                }
                this.classifyName.push(element.classifyName)
                sum  = sum + Number(element.periodSum)
                obj.name = element.classifyName
                obj.value = element.periodSum
                arr.push(obj)
            });
            this.sumNumber = sum
            this.periodSums = arr
            if(newVal){
                this.myinitChart3();
            }
        }
    },
    data(){
        return{
            classifyName:[],
            sumNumber:'',
            periodSums:[]
        }
    },
    mounted() {
        // this.$nextTick(() => {
            setTimeout(() => {
               this.myinitChart3();
            }, 400); 
          
        // });
    },
    methods:{
        myinitChart3(){
            this.chart = echarts.init(document.getElementById("PieCircle3"));
            var option = {
                animation: true,
                toolbox: {
                    show: true,
                    top: 20,
                    right: 30,
                    feature: {
                        saveAsImage: {
                            show: true,
                        },
                    }
                },
                title: {
                    text: this.sumNumber,
                    subtext: "总学时",
                    x: "center",
                    y: "center",
                    textStyle: {
                        color: "#FF8F44",
                        fontSize: 40,
                        fontWeight: "normal",
                        align: "center",
                        width: "200px"
                    },
                    subtextStyle: {
                        color: "#aaa",
                        fontSize: 20,
                        fontWeight: "normal",
                        align: "center"
                    }
                },
                legend: {
                    width: "80%",
                    left: "center",
                    textStyle: {
                        color: "#696969",
                        fontSize: 14
                    },
                    icon: "circle",
                    right: "0",
                    bottom: "0",
                    padding: [30, 30],
                    // itemGap: 20,
                    // data: ["专业基础", "通识教育", "论文", "自然科学",]
                    data:this.classifyName
                },
                tooltip: {
                    trigger: 'item',
                    formatter:function (params){
                        var str=  "分类："+ params.name +"</br>"+"占比："+  ( params.percent).toFixed(2)+"%";
                        return  str ;
                    }
                },
                series: [{
                    type: "pie",
                    // center: ["50%", "50%"],
                    radius: ["36%", "50%"],
                    color: ["#FFCB53", "#FFB141", "#FF8D27", "#FFF281", ],
                    // startAngle: 135,
                    labelLine: {
                        normal: {
                            length: 8,     //折线
                            length2: 8,  //文字下面的线
                        }
                    },
                    label: {
                        normal: {
                            // formatter: "{b|{b}}",
                            formatter: function(params) {
                                if (params.name.length > 7) {
                                    return "{a|" + params.name + ":}" + "\n" + "{b|" +( params.percent).toFixed(2)+"%}";
                                }else {
                                    return "{a|" + params.name + ":}" +"{b|" +( params.percent).toFixed(2)+"%}";
                                }
                            },
                            borderColor: "transparent",
                            borderRadius: 4,
                            rich: {
                                a: {
                                    color: "#999",
                                    lineHeight: 22,
                                    align: "center"
                                },
                                hr: {
                                    borderColor: "#aaa",
                                    width: "100%",
                                    borderWidth: 1,
                                    height: 0
                                },
                                b: {
                                    color: "#696969",
                                    fontSize: 14,
                                    lineHeight: 33
                                },
                                c: {
                                    fontSize: 14,
                                    color: "#eee"
                                },
                                per: {
                                    color: "#696969",
                                    fontSize: 14,
                                    padding: [5, 8],
                                    borderRadius: 2
                                }
                            },
                            textStyle: {
                                color: "#fff",
                                fontSize: 14
                            }
                        }
                    },
                    data: this.periodSums
                }, {
                    type: "pie",
                    radius: ["35%", "36%"],
                    label: {
                        show: false
                    },
                }]
            }
            this.chart.setOption(option)
        } 
    }
}
</script>
<style lang="scss">
    .echarts_p{
        position: absolute;
        top: 29px;
        left: 45px;
    }
</style>


