import PieCircle1 from "@/components/echaars/PieCircle1"
import PieCircle2 from "@/components/echaars/PieCircle2"
import PieCircle3 from "@/components/echaars/PieCircle3"
import echarts1 from "@/components/echaars/echars1"
import echarts2 from "@/components/echaars/echars2"
import pinechars from '@/components/echaars/pinechars'
import echarts3 from "@/components/echaars/echars3"
import echarts4 from "@/components/echaars/echars4"
// import Courseifon from "@/views/teachers/Courseifon ";
import Courseifonke from "@/views/teachers/Courseifonke";
import trainingProgramApi from "@/api/trainingProgramApi";
import loginRequest from '@/api/login'
import Self from "@/api/self";
export default {
    name: 'CollegeSpecialty',
    components: {PieCircle1,PieCircle2,PieCircle3,echarts1,echarts2,echarts3,pinechars,echarts4,Courseifonke,},
    data(){
        return{
            zhuayeifon:'',
            specialOptions: [],
            specialId: '',
            versionOptions: [],
            // versionId: '',
            pointListke: {},
            page:{
                grade: '',
                page: 1,
                rows: 15
            },
            requirement:true,
            requirement1:false,
            requirement2:false,
            total:0,
            PieCircle1:[],
            PieCircle2:[],
            PieCircle3:[],
            chartsData1: {},
            seriesBarData:{},
            chartsDatafirst:{},
            pointListcourse:{},
            claimslist:[],
            tableDatapoints:[],
            tablepoints:[],
            tableDatake:[],
            totalke:0,
            active:0,
            pointList:{},
            pointList1:[],
            showback:false,
            showfirst:true,
            showecharts3:false,
            showecharts4:false,
            options:[],
            options1:[
                
            ],
            cities: [],
            value: "",
            cities1: [],
            value1: "",
            options2:[
                {
                    name:'全部',
                    id:''
                },
                {
                    name:'2016级',
                    id:'2'
                }
            ],
            options3:[
                {
                    name:'未达成',
                    id:'0'
                },
                {
                    name:'已达成',
                    id:'1'
                }
            ],
            tableDatafen:[],
            tableDatagoals:[],
            listimg:[
                {
                    imgurl:'iconxueyuan',
                    // cont:'机械工程学院',
                    title:'所属学院'
                },
                {
                    imgurl:'iconxuezhi',
                    // cont:'4年制',
                    title:'学制'
                },
                {
                    imgurl:'iconbanji',
                    // cont:'8',
                    title:'在校班级'
                },
                {
                    imgurl:'iconxuesheng',
                    // cont:'237',
                    title:'学生'
                },
                {
                    imgurl:'iconfuzeren',
                    // cont:'赵栓峰',
                    title:'专业负责人'
                },
                {
                    imgurl:'iconrenzhengzhuangtai',
                    cont:'',
                    title:'认证状态'
                },
            ],
            lightning:[
                // {
                //     imgurl:'iconyujing',
                //     cont:'未完成的指标点建设',
                //     status:1
                // },
                {
                    imgurl:'iconyujing',
                    // cont:'对2015级学生毕业要求能力达成评价分析发现，毕业要求7-环境和可持续发展达成度为0.614，在12项毕业要求能力中达成最弱，其中毕业要求指标点毕业要求指标点7-1：能够正确理解与本专业工程实践相关的环境与社会可持续发展的重要性、内涵和要求，树立节约资源、环境友好的基本理念能力达成相对较弱请注意分析原因。',
                    cont:'暂无',
                    status:0
                },
                // {
                //     imgurl:'iconyujing',
                //     cont:'毕业要求3：开发/解决方案达成度为0.61，低于目标值0.65',
                //     status:0
                // },
            ],
            Trainings:[
                {
                    title:'培养目标',
                    id:1
                },
                {
                    title:'毕业要求',
                    id:1
                },
                {
                    title:'课程体系',
                    id:1
                },
            ],
            bianhaolist:[],
            content:'',
            versionVal:'',
            pieCircleData:[],
            page1: {
                auth:'',
                grade:'',
                classid:'',
                page: 1,
                rows: 15
            },
            fieldCoursePropertyOptions: [],
            courseClassification: [],
            pagetype: {
                versionId: "",
                classificationId: "",
                courseProperty: null,
                keywords: "",
                term: "",
                page: 1,
                rows: 15
            },
            datalist:[],
            courseList:[],
            name:'',
            contentzy:'',
            id:0,
            index:'',
            isActive:0,
            userIdentity: '',
            versionList: [],
            versionId: '',
            versionIdtixi:0,
            roleName: '',
            gradesvul:'',
            SpecialVariable:0,
            specialtyName:'',
            specialtyNames:'',
            countDown:'',
            specialtyId:this.$route.query.id || localStorage.getItem('specialtyId'),
            totalstudent:0,
            name: '',
        }
    },
    created() {
        this.roleName = localStorage.getItem('roleName')
        
    },
    mounted(){
        
        this.initDatake()
        this.courseClassificationAll()
        this.fieldCourseProperty()
        // 新接口
        this.getversionid()
        this.getstudent()
      },
      methods: {
        changeversionId(){
            this.getclaimlist(this.versionId)
            this.getanalyze(this.versionId)
            this.grades(this.versionId)
            this.initDatake(this.versionId)
            this.getstudent('',this.versionId)
        },
        //  获取版本号basic/specialty/portrayal/version?specialtyId=22222222   get   版本号
        getversionid(){
            this.$get(`basic/specialty/portrayal/version?specialtyId=${this.specialtyId}`).then(res=>{
                if(res.status == 1){
                    res.data.forEach(element => {
                        this.versionOptions = element.version
                    });
                    this.versionId = this.versionOptions[0].id
                    // this.getclaimlist(this.versionId)
                    // this.getanalyze(this.versionId)
                    this.grades(this.versionId)
                    this.initDatake(this.versionId)
                    this.getanalyze()
                }
            })
        },
        // 毕业要求basic/specialty/portrayal/claimlist?versionId=22222222   get 毕业要求
        getclaimlist(versionId){
            let arr = versionId || this.versionId
            this.$get(`basic/specialty/portrayal/claimlist?versionId=${arr}`).then(res=>{
                if(res.status == 1){
                    if(res.data.length > 0){
                        this.datalist = res.data
                        this.getPointCourse(this.datalist[0].id,this.datalist[0],0)
                    }else {
                        this.datalist = []
                        this.courseList = []
                    }
                    
                }
            })
        },
        // basic/specialty/portrayal/analyze?specialtyId=22222222&versionId=11111111
        getanalyze(versionId){
            // 接收路由传递过来的  参数专业id
            let arr = versionId || this.versionId
            this.$get(`basic/specialty/portrayal/analyze?specialtyId=${this.specialtyId}&versionId=${arr}`).then(res=>{
                if(res.status == 1){
                    this.countDown = res.data.specialtyCode
                    this.listimg[0].cont = res.data.academyName
                    this.listimg[1].cont = res.data.lengthOfSchooling + '年制'
                    this.listimg[2].cont = res.data.classCount+ '个'
                    this.listimg[3].cont = res.data.studentCount+ '人'
                    this.listimg[4].cont = res.data.learder
                    if(res.data.authenticationStatus === 0){
                        this.listimg[5].cont = '已认证'
                    }else if(res.data.authenticationStatus === 1){
                        this.listimg[5].cont = '未认证'
                    }else if(res.data.authenticationStatus === 2){
                        this.listimg[5].cont = '已申请'
                    }
                    this.specialtyNames = res.data.specialtyName
                    this.zhuayeifon = res.data.specialtyIntro
                    if(res.data.trainTarget.list !=null){
                        this.bianhaolist = res.data.trainTarget.list
                    }else {
                        this.bianhaolist = []
                    }
                    if(res.data.trainTarget.target != null) {
                        this.versionVal = res.data.trainTarget.target.name
                    }else {
                        this.versionVal = ''
                    }
                    if(res.data.trainTarget.target !=null){
                        this.contentzy = res.data.trainTarget.target.content
                    }else {
                        this.contentzy = ''
                    }
                    
                    // this.$nextTick(() => {
                        this.PieCircle1 = res.data.analyzes
                        this.PieCircle2 = res.data.analyzes
                        this.PieCircle3 = res.data.analyzes
                    // });
                    
                    let obj = {
                        versionId:arr,
                        courses:res.data.courses
                    }
                    this.pointListke = obj
                }
            })
        },
        changegrade(ids){
            this.getstudent(ids.slice(0,4),this.versionId)
            this.allClass(ids.slice(0,4))
        },
        changeclass(){
            this.getstudent('',this.versionId)
        },
        changeauth(){
            this.getstudent('',this.versionId)
        },
        // 个体学生追踪
        getstudent(ids,versionId){
            let key ={
                page:this.page1.page,
                rows:this.page1.rows,
                specialtyId:this.specialtyId,
                grade:ids || this.page1.grade.slice(0,4),
                classId:this.page1.classid,
                auth:this.page1.auth,
                versionId:versionId,
            }
            this.$post('/basic/specialty/portrayal/student',key).then(res=>{
                if (res.status == 1) {
                    this.tableDatafen = res.data.list;
                    this.totalstudent = Number(res.data.total);
                }
            })
        },
        
        //  年级查询
        grades(versionId) {
            let arr = versionId || this.versionId
            this.$get(`/basic/achieve/gradesbyVersionID/${arr}/${this.specialtyId}`).then(res => {
                if (res.status == 1) {
                    this.cities = res.data
                    this.options = res.data
                    this.gradesvul = res.data[1]
                    this.initData(res.data[1],arr)
                }
            })
        },
        changes1(ids){
            this.allClass(ids.slice(0,4))
            this.initData(ids.slice(0,4),this.versionId)
        },
        //   /basic/achieve/allClass   班级查询
        allClass(grade) {
            let url = `/basic/achieve/classes/${grade}/${this.specialtyId}/v1`
            this.$get(url).then(res => {
                if (res.status == 1) {
                    this.cities1 = res.data
                    this.cities1.unshift({id:null,name:'全部'})
                    this.options1 = res.data
                    // this.options1.unshift({id:null,name:'全部'})
                }
            })
        },
        initData(a,versionId){
            this.showfirst = true
            this.showback = false
            this.showecharts3 = false
            this.showecharts4 = false
            let data1 = {
                grade: this.value || a ,
                classId: this.value1 ,
                specialtyId :this.specialtyId,
                versionId:versionId,
            }
            this.$post('/basic/achieve/analysis/claim', data1).then(res => {
                if (res.status == 1) {
                    this.falgeshow = true
                    this.claimslist = res.data.claims
                    this.chartsDatafirst = res.data 
                    this.title = res.data.title
                } else {
                    this.$notify({
                        title: '提示',
                        message: res.message,
                        type: 'error',
                        duration: '1500'
                    });
                }
                this.flag = false
            }).catch(err => {
                this.$notify({
                    title: '提示',
                    message: err.message,
                    type: 'error',
                    duration: '1500'
                });
                this.flag = false
            })
            // this.chartsDatafirst = data
        },
        
        initDatake(versionId) {
            const url = '/basic/course/list4specialtyId2'
            this.pagetype.versionId = versionId
            this.pagetype.specialtyId = this.specialtyId
            this.$post(url, this.pagetype).then(res => {
                if (res.status === 1) {
                this.tableDatake = res.data;
                this.totalke = res.total
                } else {
                this.$notify.error({
                    title: "error",
                    duration: 1500,
                    message: res.message
                });
                }
            });
        },
            //课程分类
        courseClassificationAll() {
            const url = trainingProgramApi.courseClassificationAll;
            this.$get(url).then(res => {
                if (res.status === 1) {
                const allArr = {
                    id: "",
                    name: "全部"
                };
                this.courseClassifications = JSON.parse(JSON.stringify(res.data));
                this.courseClassification = res.data;
                this.courseClassification.unshift(allArr);
                } else {
                this.$notify.error({
                    title: "error",
                    duration: 1500,
                    message: res.message
                });
                }
            });
        },
        //课程性质（课程类型）
        fieldCourseProperty() {
            const url = trainingProgramApi.fieldCourseProperty;
            this.$get(url).then(res => {
                if (res.status === 1) {
                const allArr = {
                    code: null,
                    value: "全部"
                };
                this.fieldCoursePropertyOptionsed = JSON.parse(
                    JSON.stringify(res.data)
                );
                this.fieldCoursePropertyOptions = res.data;
                this.fieldCoursePropertyOptions.unshift(allArr);
                } else {
                this.$notify.error({
                    title: "error",
                    duration: 1500,
                    message: res.message
                });
                }
            });
        },
        search() {
            // this.resetTemp();
            this.initDatake();
        },
        getClaim(id) {
        },
        jump(id){
            this.$router.push({path: '/studentside/graduation',query: {id: id, title:"毕业要求"}})
        },
        //information/information/getGraduationClaim
        getGraduationClaim(){  //学生端
            this.$get('/info/information/getGraduationClaim').then(res=>{
                if(this.datalist.length > 0) {
                    this.datalist = res
                    this.getPointCourse(this.datalist[0].id,this.datalist[0],0)
                }
            })
        },
        // /info/information/getPointCourse   学生端
        getPointCourse(id,row,index){
            this.name = row.name
            this.isActive = index
            this.content = row.content
            this.id = id
            this.index = index
            localStorage.setItem("index",index)
            localStorage.setItem('id',id)
            localStorage.setItem('content',row.content)
            localStorage.setItem('name',row.name)
            if(localStorage.getItem("index")){
                this.isActive = localStorage.getItem("index")
            }
            this.$post(`basic/specialty/portrayal/pointlist?graduationId=${id}`).then(res=>{
                this.courseList = res.data
            })
        },
        go(status){
            this.$router.push({
                path: status ===1 ? '/professionalSystem/collegeCourse' : '/professionalSystem/collegeStudent'
                })
        },
        goback(){
            this.$router.go(-1)
        },
        keoperateRow(id){
            this.$router.push({
            path:'/professionalSystem/collegeCourse',
                query:{
                    id:id
                }
            })
        },
        operateRow(row){
            this.$router.push({
            path:'/professionalSystem/collegeStudent',
                query:{
                    row:row,
                    versionId:this.versionId,
                    specialtyId:this.specialtyId,
                }
            })
        },
        //返回上级
        gobackpoit(){
            if(this.SpecialVariable == 0){
                this.showfirst = true
                this.showback = false
                this.showecharts3 = false
                this.showecharts4 = false
            }else if (this.SpecialVariable == 1){
                this.SpecialVariable =0
                this.showfirst = true
                this.showback = false
                this.showecharts3 = false
                this.showecharts4 = false
            }else if(this.SpecialVariable == 2){
                this.SpecialVariable =1
                this.showfirst = false
                this.showback = true
                this.showecharts3 = false
                this.showecharts4 = false
            }else if(this.SpecialVariable == 3){
                this.SpecialVariable =2
                this.showfirst = false
                this.showback = false
                this.showecharts3 = true
                this.showecharts4 = false
            }
        },
        handdle(item){
            this.SpecialVariable = 1
            this.showback = true
            this.showfirst =false
            let key = {
                claimId: item.claimId,
                grade:  this.value || this.gradesvul,
                classId: this.value1,
                studentId: "",
                specialtyId :this.specialtyId,
            }
            this.$post('/basic/achieve/analysis/point', key).then(res => {
                if (res.status == 1) {
                    this.tableDatapoints = res.data.points
                    this.title = res.data.title
                    this.pointList = res.data
                }
            })
        },
        handdle1(item){
            this.SpecialVariable =2
            this.showecharts3 = true
            this.showback = false
            this.showfirst =false
            let key = {
                pointId:item.pointId,
                grade: this.value || this.gradesvul,
                classId: this.value1,
                specialtyId :this.specialtyId,
            }
            this.$post('/basic/achieve/analysis/course',key).then(res=>{
                if (res.status === 1) {
                    this.title = res.data.title
                    //表格
                    this.tablepoints = res.data.points
                    //柱状图
                    const seriesBarObj = {
                        pointcode:item.pointcode,
                        pointname:item.pointname,
                        legendData: res.data.sign,
                        xAxisData: res.data.coursename,
                        seriesData: [{
                                name: res.data.target.name,
                                barGap: "5%",
                                data: res.data.target.value || [],
                                markPoint: {
                                    data: [{
                                            type: 'max',
                                            name: '最大值'
                                        },
                                        {
                                            type: 'min',
                                            name: '最小值'
                                        }
                                    ]
                                }
                            },
                            {
                                name: res.data.analysis.name,
                                barGap: "5%",
                                data: res.data.analysis.value || []
                            }
                        ]
                    }
                    this.pointListcourse = seriesBarObj
                    //饼图
                    res.data.cake.forEach(element => {
                        element.pointcode=item.pointcode
                    });
                    this.pieCircleData = res.data.cake
                } else {
                    this.$notify.error({
                        title: 'error',
                        duration: 1500,
                        message: res.message
                    })
                }
            })
        },
        handdle2(item){
            this.SpecialVariable =3
            this.showecharts4 = true
            this.showecharts3 = false
            this.showback = false
            this.showfirst =false
            let key = {
                courseId:item.courseId,
                grade:this.value || this.gradesvul,
                classId: this.value1,
                specialtyId :this.specialtyId,
            }
            this.$post('/basic/achieve/analysis/goals',key).then(res=>{
                if (res.status === 1) {
                    this.title = res.data.title
                    this.tableDatagoals = res.data.goals
                    this.seriesBarData = res.data
                } else {
                    this.$notify.error({
                        title: 'error',
                        duration: 1500,
                        message: res.message
                    })
                }
            })
        },
        tabswitch(index){
            if(index == 0){
                this.requirement = true
                this.requirement1 = false
                this.requirement2 = false
            }else if(index == 1){
                this.requirement = false
                this.requirement1 = true
                this.requirement2 = false
                this.getclaimlist(this.versionId)
            }else if(index ==2){
                this.requirement = false
                this.requirement1 = false
                this.requirement2 = true
                // this.getInfotixi()
                
            }
            this.active = index
        },
        handleSizeChange(val) {
            this.pagetype.rows = val;
            this.initDatake();
            },
        handleCurrentChange(obj) {
            this.pagetype.page = obj;
            this.initDatake();
        },
        handleSizeChange1(val) {
            this.page1.rows = val;
            this.getstudent();
            },
        handleCurrentChange2(obj) {
            this.page1.page = obj;
            this.getstudent();
        },
    }
}


