<template>
<div class="college-full-screen college Mixeddataifon1">
    <div class=" Mixeddataifon">
            <!-- 学制学年  缺字段 -->
        <div class="Mixe_dv">
            <div class="Mixe_top">
                <div class="Mixe_left" @click="goback" v-if="this.$route.query.id">
                    <i style="color:#b0f9e0" class="el-icon-back"></i>
                    <span>返回学院</span>
                </div>
                <div class="Mixe_cont" >
                    <div class="Mixe_svgIcon">
                        <svgIcon icon-class="iconzhuanye1" style="font-size: 39px;margin-top: 20px;"></svgIcon>
                    </div>
                    <h2>{{specialtyNames}}</h2>
                    <!-- 鼠标的移入  移出 -->
                    <span>({{countDown}})</span>
                </div>
                <div class="Mixe_right1" >
                    <el-select v-model="versionId" placeholder="请选择版本" style="width: 100%" @change="changeversionId">
                        <el-option v-for="item in versionOptions"
                            :key="item.id"
                            :label="item.versionName"
                            :value="item.id">
                        </el-option>
                    </el-select>
                </div>
            </div>
            <div class="Mixe_btn">
                <ul>
                    <li v-for="(item,index) in listimg" :key="index">
                        <div class="Mixe_listimg">
                            <svgIcon :icon-class="item.imgurl" style="font-size: 20px;margin-top: 15px;"></svgIcon>
                        </div>
                        <span>{{item.title}}</span>
                        <span>{{item.cont}}</span>
                    </li>
                </ul>
            </div>
        </div>
        <!-- 预警信息  缺接口 -->
        <div class="Mixe_brief">
            <div class="brief_left">
                <h2>专业简介</h2>
                <p>{{zhuayeifon}}</p>
            </div>
            <div class="brief_right">
                <h2>预警信息</h2>
                <ul>
                    <li v-for="(item,index) in lightning" :key="index">
                    <svgIcon :icon-class="item.imgurl" v-if="item.status == 1" style="font-size: 40px;color: #FF5E62;float: left;margin: 0 29px 0 50px; margin-left: 0;"></svgIcon>
                    <svgIcon :icon-class="item.imgurl" v-if="item.status == 0" style="font-size: 40px;color: #FF8F44;float: left;margin: 0 29px 0 50px; margin-left: 0;"></svgIcon>
                    <span v-if="item.status == 1">{{item.cont}}</span>
                    <span v-if="item.status == 0" style="color: #FF8F44;">{{item.cont}}</span>
                    </li>
                </ul>
            </div>
        </div>
        <!-- // 培养目标 -->
        <div class="Mixe_Training " >
            <div>
                <ul>
                    <li v-for="(item,index) in Trainings" :key="index" @click="tabswitch(index)" :class="{isactive:active == index}" style="width:150px;height:40px;">
                        <span :class="{spanactive:active == index}">{{item.title}}</span>
                    </li>
                </ul>
            </div>
             <!-- // 培养目标 -->
            <div class="Mixe_Trainingtop" v-if="requirement">
                <div class="Training_top">
                    <h2>{{versionVal}}培养目标</h2>
                    <p v-html="contentzy"></p>
                </div>
                <div class="Training_bto">
                    <div  class="Training_bto1" v-for="(item,index) in bianhaolist" :key="index">
                        <img v-if="(index + 1) % 4 === 1 || (index + 1) % 4 === 2" src="@/assets/img/bianhao.png" alt="">
                        <img v-if="(index + 1) % 4 === 3 || (index + 1) % 4 === 0" src="@/assets/img/bianhao2.png" alt="">
                        <p>{{item.code}}</p>
                        <span >
                            {{item.content}}
                        </span>
                    </div>
                </div>
            </div>
            <!-- 毕业要求 -->
            <div class="center_content" v-if="requirement1">
                <div class="requirement-side1">
                    <h3>毕业要求</h3>
                    <ul>
                        <li :class="{ active: isActive == index  }"  v-for="(item, index) in datalist" :key="index" @click="getPointCourse(item.id,item,index)">
                            <span style="overflow: hidden;white-space: nowrap;text-overflow: ellipsis;">
                                毕业要求{{index+1}}:{{item.name}}
                            </span>
                        </li>
                    </ul>
                </div>
                <div class="requirement-main1">
                    <keep-alive>
                        <div class="requirement-title"  v-if="datalist.length == 0 ? false:true">
                            <label>毕业要求{{Number(isActive) + 1}}:&nbsp;&nbsp;</label><span>{{name}}：{{content}}</span>
                        </div>
                    </keep-alive>
                
                    <div class="requirement-item1">
                        <ul class="requirement-item_ul">
                            <li v-for="(item, index) in courseList" :key="index" class="requirement-item_li">
                                <div class="item-lf">{{item.code}}</div>
                                <div class="item-rt">
                                    <p>指标点{{item.code}}</p>
                                    <span>{{item.content}}</span>
                                    <ul>
                                        <li v-for="(items, index) in item.courseList" :key="index" @click="jump(items.courseId)">
                                        {{items.courseName}}
                                        </li>
                                    </ul>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
            <!-- 课程体系 -->
            <div class="Curriculum-dv2" v-show="requirement2">
                <Courseifonke :pointListske="pointListke"></Courseifonke>
            </div>
        </div>
        <!-- 三张 饼图 缺接口 -->
        <div class="Mixe_PieCircle">
            <div >
                <!-- 饼图应用接口数据-->
                <PieCircle1 :chartsDataone="PieCircle1" class="Mixe_PieCircle1"></PieCircle1>
                <PieCircle2 :chartsDatatwo="PieCircle2" class="Mixe_PieCircle2"></PieCircle2>
                <PieCircle3 :chartsDatatree="PieCircle3" class="Mixe_PieCircle3"></PieCircle3>
            </div>
        </div>
        <div class="Mixe_search">
            <div class="Mixe_search_lf">
                <span class="cl-btn">课程列表</span>
            </div>
            <div class="Mixe_sear_rt">
                <div class="Mixe_search_rt">
                    <el-select v-model="pagetype.courseProperty" placeholder="请选择选修状态">
                    <el-option
                        v-for="item in fieldCoursePropertyOptions"
                        :key="item.code"
                        :label="item.value"
                        :value="item.code"
                    ></el-option>
                    </el-select>
                </div>
                <div class="Mixe_search_rt">
                    <el-select v-model="pagetype.classificationId" placeholder="请选择课程类型" @change="search">
                        <el-option
                            v-for="item in courseClassification" 
                            :key="item.id"
                            :label="item.name"
                            :value="item.id"
                        ></el-option>
                    </el-select>
                </div>
                <div class="Mixe_search_rt">
                    <el-input
                        class="search"
                        @keyup.enter.native="search"
                        v-model="pagetype.keywords"
                        placeholder="请输入搜索关键词"
                        clearable
                        ></el-input>
                </div>
            </div>
        </div>
        <div class="Mixe_table">
            <el-table :data="tableDatake" stripe style="width: 100%">
                <el-table-column prop="code" label="课程编码"></el-table-column>
                <el-table-column prop="name" label="课程名称"></el-table-column>
                <el-table-column prop="classificationVal" label="课程类型"></el-table-column>
                <el-table-column prop="credit" label="总学分"></el-table-column>
                <el-table-column prop="period" label="总学时"></el-table-column>
                <el-table-column prop="termVal" label="开课学期"></el-table-column>
                <el-table-column prop="leaderName" label="课程负责人"></el-table-column>
                <el-table-column prop="constructionSituation" label="课程建设情况"></el-table-column>
                <el-table-column label="操作" width="150">
                    <template slot-scope="scope">                  
                        <div class="cl-operate-button distance">
                            <SvgIcon icon-class="iconchakanwenjuan"></SvgIcon> 
                            <el-button
                            @click.native.prevent="keoperateRow(scope.row.id)"
                            type="text"
                            size="small">
                            课程分析
                            </el-button>
                        </div>                    
                    </template>
                </el-table-column>
                </el-table>
            <div class="college_pagination">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    background
                    :current-page="page.page"
                    :page-sizes="[15,30,40,50]"
                    :page-size="page.rows"
                    layout="prev, pager, next, jumper, sizes, total"
                    :total="totalke"
                ></el-pagination>
            </div>
        </div>
        <div class="Mixe_search1">
            <div class="Mixe_search1_lf">
                <span class="cl-btn">毕业要求达成情况</span>
            </div>
            <div class="Mixe_back" >
                <svgIcon icon-class="iconfanhui" style="font-size: 14px;color: #DDDDDD;"></svgIcon>
                <span @click="gobackpoit">返回上级</span>
            </div>
            <div class="Mixe_sear1_rt">
                <div class="Mixe_search1_rt">
                    <el-select v-model="value" placeholder="请选择" @change="changes1">
                    <el-option
                        v-for="item in cities"
                        :key="item" 
                        :label="item" 
                        :value="item"
                    ></el-option>
                    </el-select>
                </div>
                <div class="Mixe_search1_rt">
                    <el-select v-model="value1" placeholder="请选择" @change="initData">
                    <el-option
                        v-for="item in cities1"
                        :key="item.id" 
                        :label="item.name" 
                        :value="item.id"
                    ></el-option>
                    </el-select>
                </div>
            </div>
        </div>
        <div class="bgc-fff" v-show="showfirst">
            <div class="echarts"  >
                <!-- 雷达图引用 -->
                <echarts1 :chartsData="chartsDatafirst"></echarts1>
            </div>
            <div  class="chartsData_left"  >
                <ul>
                    <li v-for="(item,index) in claimslist" :key="index" @click="handdle(item)">
                        <p class="chartsData_p">
                            <span class="chartsData_sp"></span>
                            <i>{{index+1}}</i>
                        </p>
                        <div class="chartsData_leftdv">
                            <p class="chartsData_frist">毕业要求{{index+1}}：{{item.claimcontent}}</p>
                            <p class="chartsData_tow">支撑目标：{{item.goals}}</p>
                            <div class="chartsData_bottom">
                                <span >
                                    直接达成值：{{item.analysis}}
                                </span>
                                <span >
                                    间接达成值：{{item.score}}
                                </span>
                                <span >
                                    期望达成值：{{item.target}}
                                </span>
                            </div>
                        </div>
                        
                    </li>
                </ul>
            </div>
        </div>
        <div class="bgc-ffftwo" v-show="showback">
            <!-- <div class="DegreeAchievement-dv7" v-if="title">
                <span>{{title}}</span>
            </div> -->
            <div class="echarts2">
                <!-- 柱状图引用 -->
                <echarts2 :pointLists="pointList"></echarts2>
            </div>
            <div  class="chartsData1_left">
                <ul>
                    <li v-for="(item,index) in tableDatapoints" :key="index" @click="handdle1(item)">
                        <p class="chartsData1_p">
                            <span class="chartsData1_sp"></span>
                            <i>{{index+1}}</i>
                        </p>
                        <div class="chartsData1_leftdv">
                            <p class="chartsData1_frist">毕业要求指标点{{item.pointcode}}：{{item.pointname}}</p>
                            <p class="chartsData1_tow">支撑课程：{{item.courses}}</p>
                            
                        </div>
                        <div class="chartsData1_bottom">
                            <span >
                                直接达成值：{{item.analysis}}
                            </span>
                            <span >
                                间接达成值：{{item.score}}
                            </span>
                            <span >
                                期望达成值：{{item.target}}
                            </span>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        <div class="bgc-fffsre" v-show="showecharts3">
            <div class="echarts3" >
                <!-- 柱状图引用 -->
                <echarts3 :pointListcourses="pointListcourse"></echarts3>
            </div>
            <div class="analysis_body1">
                <div class="PieCircle_class">
                    <pinechars :pieCircleData = pieCircleData></pinechars>
                </div>
                <div class="analysis_body_table1">
                    <el-table :data="tablepoints" stripe height="250" style="width: 100%" >
                        <el-table-column prop="coursename" label="课程名称" width="200">
                            <template slot-scope="scope"  @click="handdle2(scope.row)">
                                <span style="margin-left: 10px; color:#03B187; font-size:14px;cursor: pointer;" @click="handdle2(scope.row)">{{scope.row.coursename}}</span>
                            </template>
                        </el-table-column>
                        <el-table-column prop="examine" label="考核方式">
                        </el-table-column>
                        <el-table-column prop="analysis" label="指标点评价值" align="center" width="120">
                        </el-table-column>
                        <el-table-column prop="brace" label="支撑权重" align="center" width="100">
                        </el-table-column>
                    </el-table>
                </div>
            </div>
        </div>
        <div class="bgc-ffffove" v-show="showecharts4">
            <div class="echarts4" >
                <!-- 柱状图引用 -->
                <echarts4 :seriesBars="seriesBarData"></echarts4>
            </div>
            <div class="analysis_body">
                <div class="analysis_body_table">
                    <el-table :data="tableDatagoals" stripe style="width: 100%">
                        <el-table-column prop="goalname" label="课程目标" align="center">
                        </el-table-column>
                        <el-table-column prop="point" label="对应能力培养" align="center">
                        </el-table-column>
                        <el-table-column prop="examines" label="考核方式" align="center">
                        </el-table-column>
                        <el-table-column prop="score" label="考核分值" align="center">
                        </el-table-column>
                        <el-table-column prop="zhanbi" label="考核分值占比" align="center">
                        </el-table-column>
                        <el-table-column prop="analysis" label="直接达成度" align="center">
                        </el-table-column>
                        <el-table-column prop="agent" label="间接达成度" align="center" >
                            <template slot-scope="scope">
                                <span >{{ scope.row.agent == 'null'? '': scope.row.agent}}</span>
                            </template>
                        </el-table-column>
                        <el-table-column prop="target" label="期望达成度" align="center">
                        </el-table-column>
                    </el-table>
                </div>
            </div>
        </div>
        <div class="Mixe_searchke">
            <div class="Mixe_searchke_lf">
                <span class="cl-btn">个体学生追踪</span>
            </div>
            <div class="Mixe_searke_rt">
                <div class="Mixe_searchke_rt">
                    <el-select v-model="page1.grade" placeholder="请选择年级" @change="changegrade">
                    <el-option
                        v-for="items in options"
                        :key="items"
                        :label="items"
                        :value="items"
                    ></el-option>
                    </el-select>
                </div>
                <div class="Mixe_searchke_rt">
                    <el-select v-model="page1.classid" placeholder="请选择班级" @change="changeclass">
                    <el-option
                        v-for="item in options1"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id"
                    ></el-option>
                    </el-select>
                </div>
                <div class="Mixe_searchke_rt">
                    <el-select v-model="page1.auth" placeholder="请选择达成状态" @change="changeauth">
                    <el-option
                        v-for="item in options3"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id"
                    ></el-option>
                    </el-select>
                </div>
            </div>
        </div>
        <!-- 学生追踪列表数据  缺接口 -->
        <div class="Mixe_tableke">
            <el-table :data="tableDatafen" stripe style="width: 100%">
                <el-table-column prop="code" label="学号"></el-table-column>
                <el-table-column prop="name" label="姓名"></el-table-column>
                <el-table-column prop="grade" label="入学年级"></el-table-column>
                <el-table-column prop="academy_name" label="所属学院"></el-table-column>
                <el-table-column prop="specialty_name" label="所属专业"></el-table-column>
                <el-table-column prop="class_name" label="班级"></el-table-column>
                <el-table-column label="达成状态">
                    <template slot-scope="scope">
                    <div v-if="scope.row.auth === 0">未达成 </div>
                    <div v-if="scope.row.auth === 1">已达成</div>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="150">
                    <template slot-scope="scope">                  
                        <div class="cl-operate-button distance">
                            <SvgIcon icon-class="iconchakanwenjuan"></SvgIcon> 
                            <el-button
                            @click.native.prevent="operateRow(scope.row)"
                            type="text"
                            size="small">
                            分析报告
                            </el-button>
                        </div>                    
                    </template>
                </el-table-column>
            </el-table>
            <div class="college_pagination">
                <el-pagination
                    @size-change="handleSizeChange1"
                    @current-change="handleCurrentChange2"
                    background
                    :current-page="page.page"
                    :page-sizes="[15,30,40,50]"
                    :page-size="page.rows"
                    layout="prev, pager, next, jumper, sizes, total"
                    :total="totalstudent"
                ></el-pagination>
            </div>
        </div>
    </div>
</div>
</template>

<script>
    // js部分抽离 页面
    import major from  '@/api/major'
    export default {
    ...major
    }
</script>

<style lang='scss'>
    // css  部分抽离页面  
    @import "../../styles/majors.scss";
    @import "../../styles/college.scss";
</style>



